* { box-sizing: border-box; margin: 0; padding: 0; }

body { font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif; }

.app { height: 100vh; display: flex; flex-direction: column; background: #f5f5f5; }

.header {
  display: flex; justify-content: space-between; align-items: center;
  padding: 16px 24px; background: #2563eb; color: white;
}
.header h1 { font-size: 1.25rem; }

.upload-btn {
  padding: 8px 16px; background: white; color: #2563eb;
  border: none; border-radius: 6px; cursor: pointer; font-weight: 500;
}
.upload-btn:hover { background: #e5e5e5; }

.chat-container { flex: 1; display: flex; flex-direction: column; max-width: 800px; width: 100%; margin: 0 auto; padding: 16px; }

.messages { flex: 1; overflow-y: auto; padding: 16px 0; }

.empty-state { text-align: center; color: #888; margin-top: 40px; }

.message { margin-bottom: 12px; display: flex; }
.message.user { justify-content: flex-end; }
.message.assistant, .message.system { justify-content: flex-start; }

.message-content {
  max-width: 70%; padding: 12px 16px; border-radius: 12px; line-height: 1.4;
}
.message.user .message-content { background: #2563eb; color: white; }
.message.assistant .message-content { background: white; border: 1px solid #ddd; }
.message.system .message-content { background: #e0f2fe; color: #0369a1; font-size: 0.9rem; }

.input-form { display: flex; gap: 8px; padding-top: 16px; border-top: 1px solid #ddd; }
.input-form input {
  flex: 1; padding: 12px 16px; border: 1px solid #ddd; border-radius: 8px; font-size: 1rem;
}
.input-form input:focus { outline: none; border-color: #2563eb; }
.input-form button {
  padding: 12px 24px; background: #2563eb; color: white;
  border: none; border-radius: 8px; cursor: pointer; font-weight: 500;
}
.input-form button:disabled { background: #93c5fd; cursor: not-allowed; }
